/* --------------------------------------------------------------------------------------------------------------------------
Video modal
--------------------------------------------------------------------------------------------------------------------------------*/
/* Стили для фона модального окна */
.next-video-modal {
	display: none; /* по умолчанию скрыто */
	position: fixed;
	z-index: 9999;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	overflow: auto; /* если видео не помещается, появится прокрутка */
	background-color: rgba(0, 0, 0, 0.8); /* полупрозрачный фон */
	align-items: center;
	justify-content: center;

	&.modal--show {
		display: flex;
		animation: fade-up 0.3s ease-in-out forwards;
	}
	// content
	&__content {
		position: relative;
		max-width: 800px;
		width: calc(100vw - 30px);

		video {
			width: 100%;
			aspect-ratio: 16/9;
			object-fit: cover;
		}
	}
	// close
	&__close {
		position: absolute;
		top: 10px;
		right: 15px;
		color: #aaa;
		font-size: 28px;
		font-weight: bold;
		cursor: pointer;
		z-index: 1000;

		&:hover {
			color: #000;
		}
	}
}
