@charset "UTF-8";
@font-face {
  font-family: Playfair;
  font-display: swap;
  src: url("../fonts/Playfair-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: TildaSans;
  font-display: swap;
  src: url("../fonts/TildaSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: TildaSans;
  font-display: swap;
  src: url("../fonts/TildaSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
/* ------------------------------------------------------------------------------------------------------------------------------
ROOT
--------------------------------------------------------------------------------------------------------------------------------*/
:root {
  --site-width: 1440px;
  --container-width: 1240px;
  --desktop-padding: 100px;
  --lg-padding: 50px;
  --sm-padding: 15px;
  --font-main: "TildaSans", sans-serif;
  --font-decor: "Playfair", sans-serif;
  --black: #000;
  --white: #fff;
  --gold: #c69d07;
  --scrollbar: gray;
  --scrollbar-track: gray;
  --scrollbar-thumb: rgb(18, 18, 18);
  --gradient: linear-gradient(303deg, #f4de91 0%, #e4c559 42.31%, #e1c150 49%, #f0d883 68.27%, #f5d668 100%);
}

/* ------------------------------------------------------------------------------------------------------------------------------
RESET
--------------------------------------------------------------------------------------------------------------------------------*/
* {
  margin: 0;
  border: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus, *:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  inline-size: 100%;
  block-size: 100%;
}

body {
  overscroll-behavior: none;
  text-rendering: optimizelegibility;
  text-size-adjust: 100%;
  font-family: var(--font-main);
  font-size: 1rem;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

input[type=number] {
  appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

button {
  cursor: pointer;
  user-select: none;
  background-color: transparent;
}
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a {
  user-select: none;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:focus, a:active {
  outline: none;
}
a:visited {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sup {
  color: inherit;
}

/* ------------------------------------------------------------------------------------------------------------------------------
TYPOGRAPHY
--------------------------------------------------------------------------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-main);
}

/* ------------------------------------------------------------------------------------------------------------------------------
BASE
--------------------------------------------------------------------------------------------------------------------------------*/
html {
  scroll-behavior: smooth;
}

body {
  text-wrap: balance;
  background-color: rgb(0, 0, 0);
}

*::-webkit-scrollbar {
  inline-size: 0.625rem;
  background-color: var(--scrollbar);
}
*::-webkit-scrollbar-track {
  border-radius: 0.625rem;
  box-shadow: inset 0 0 0.375rem rgba(0, 0, 0, 0.2);
  background: var(--scrollbar-track);
}
*::-webkit-scrollbar-thumb {
  border-radius: 0.625rem;
  border: 0 none #fff;
  background-color: var(--scrollbar-thumb);
  cursor: pointer;
}
@media (any-hover: hover) {
  *::-webkit-scrollbar-thumb:hover {
    background-color: grey;
  }
}

.container {
  margin-inline: auto;
  max-inline-size: var(--site-width);
  padding-inline: var(--desktop-padding);
}
@media (max-width: 992px) {
  .container {
    padding-inline: var(--lg-padding);
  }
}
@media (max-width: 576px) {
  .container {
    padding-inline: var(--sm-padding);
  }
}

.container-site {
  margin-inline: auto;
  max-inline-size: var(--site-width);
}

.container-full {
  padding-inline: var(--desktop-padding);
  max-width: 100%;
}
@media (max-width: 992px) {
  .container-full {
    padding-inline: var(--lg-padding);
  }
}
@media (max-width: 576px) {
  .container-full {
    padding-inline: var(--sm-padding);
  }
}

.justify-content-center {
  display: flex;
}

.img-responsive {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
}

.screen-reader-text {
  position: absolute;
  left: -9999px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  clip: auto;
  background: #000; /* или другой фон для выделения */
  color: #fff; /* цвет текста */
  padding: 8px;
  z-index: 1000;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;
  white-space: nowrap !important; /* предотвращает перенос текста */
  border: 0 !important;
}

.no-scroll {
  overflow: hidden;
}

.no-event {
  pointer-events: none;
}

.image-bg {
  position: relative;
}
.image-bg img {
  display: block;
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  object-fit: cover;
  block-size: 100%;
  inline-size: 100%;
}

.wrapper {
  display: flex;
  min-block-size: 100%;
  /** Поменять на clip - если используется в проекте position: sticky */
  overflow: hidden;
  flex-direction: column;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

