/* ------------------------------------------------------------------------------------------------------------------------------
COMPONENTS
--------------------------------------------------------------------------------------------------------------------------------*/
.section__title {
  position: relative;
  z-index: 1;
  user-select: none;
  font-weight: 400;
  font-size: 2.9375rem;
  line-height: 110%;
  text-align: center;
}
.section__title .title-decor {
  font-family: var(--font-decor);
  font-style: italic;
  font-weight: 700;
  font-size: 3.125rem;
  letter-spacing: 1.9px;
}
@media (max-width: 576px) {
  .section__title .title-decor {
    font-size: 2.25rem;
  }
}
@media (max-width: 576px) {
  .section__title {
    font-size: 2.25rem;
  }
}

[class*=_block] {
  position: relative;
  z-index: 3;
  padding-block-start: 6.4375rem;
  padding-block-end: 6.4375rem;
  border-radius: 6.25rem;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (max-width: 768px) {
  [class*=_block] {
    border-radius: 3.125rem;
    padding-block: 4.375rem;
  }
}

[class*=_part] {
  position: relative;
  padding-block: 7.5625rem;
}
[class*=_part]:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-repeat: repeat-y;
  background-size: 100%;
  background-position: left top;
}
@media (max-width: 768px) {
  [class*=_part] {
    padding-block: 4.375rem;
  }
}

[class*=_lang] {
  display: inline-flex;
  align-items: center;
  gap: 0.625rem;
}
[class*=_lang] .btn-lang {
  font-weight: 400;
  font-size: 0.75rem;
  color: var(--white);
  opacity: 0.4;
  transition: opacity 0.3s ease-in-out;
}
[class*=_lang] .btn-lang:hover {
  opacity: 0.55;
}
[class*=_lang] .btn-lang.active {
  opacity: 0.8;
}

.btn-accent,
.btn-callback {
  font-weight: 700;
  font-size: 0.75rem;
  color: #4e421b;
  border-radius: 1.25rem;
  padding-inline: 1.25rem;
  min-height: 2.125rem;
  display: flex;
  align-items: center;
  background: linear-gradient(303deg, #f4de91 0%, #e4c559 42.31%, #e1c150 49%, #f0d883 68.27%, #f5d668 100%);
  transition: all 0.3s ease-in-out;
}
.btn-accent:hover,
.btn-callback:hover {
  filter: brightness(1.5);
  box-shadow: 0 0 4px 4px rgba(225, 193, 80, 0.35);
}

.burger {
  position: relative;
  display: inline-block;
  width: 1.25rem;
  height: 0.8125rem;
  flex-shrink: 0;
  cursor: pointer;
}
.burger span, .burger::before, .burger::after {
  display: block;
  content: "";
  transition: all 0.3s ease-in-out;
  position: absolute;
  left: 0;
  width: 100%;
  height: 1.5px;
  background-color: var(--black);
  border-radius: 0.625rem;
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}
.burger span {
  top: 50%;
  transform: translateY(-65%);
}
.burger.active span {
  width: 0;
  background-color: var(--white);
}
.burger.active::before {
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
  background-color: var(--white);
}
.burger.active::after {
  bottom: 50%;
  transform: translateY(50%) rotate(45deg);
  background-color: var(--white);
}

.request-form {
  color: var(--black);
  border-radius: 1.25rem;
  padding: 1.875rem;
  background-color: var(--white);
  max-width: 24.1875rem;
  min-width: 18.125rem;
  width: 100%;
}
.request-form__title {
  font-size: 1.75rem;
  text-align: center;
  margin-bottom: 0.3125rem;
}
.request-form__description {
  text-align: center;
  font-size: 0.875rem;
  margin-bottom: 2.1875rem;
}
.request-form__group {
  display: flex;
  align-items: start;
  gap: 0.625rem;
}
.request-form__group input:not([type=checkbox], [type=radio]) {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.625rem;
  width: 100%;
  min-height: 3.125rem;
  padding-inline: 0.9375rem;
  font-size: 0.875rem;
  color: var(--black);
}
.request-form__group input:not([type=checkbox], [type=radio])::placeholder {
  color: var(--black);
}
.request-form__group .custom-checkbox {
  position: relative;
  top: -5px;
}
.request-form__group:nth-of-type(1) {
  margin-bottom: 0.9375rem;
}
.request-form__group:nth-of-type(2) {
  margin-bottom: 1.1875rem;
}
.request-form .btn-accent {
  margin-top: 1.0625rem;
}
.request-form .privacy {
  position: relative;
  top: -2px;
  font-size: 0.875rem;
}
.request-form .privacy a {
  color: var(--balck);
  text-decoration: underline;
}
.request-form .privacy a:hover {
  text-decoration: none;
}

.custom-checkbox {
  flex-shrink: 0;
  appearance: none;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  width: 24px;
  height: 24px;
  background-color: var(--white);
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.custom-checkbox:checked {
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center center;
}

.custom-checkbox:hover {
  outline: 1px solid var(--accentlight);
}

.custom-checkbox:active {
  outline: 1px solid var(--accentlightest);
}

/*---------------------------------------------------------------------------------------------------------------------------------------------
Black card
-----------------------------------------------------------------------------------------------------------------------------------------------*/
.black-card {
  color: var(--white);
  border-radius: 1.25rem;
  backdrop-filter: blur(19px);
  background: #262626;
  padding: 1.25rem;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.9375rem;
}
.black-card__label {
  width: 2.5rem;
  aspect-ratio: 1/1;
  border-radius: 0.625rem;
  background: linear-gradient(303deg, #f4de91 0%, #e4c559 42.31%, #e1c150 49%, #f0d883 68.27%, #f5d668 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--black);
  font-weight: 700;
}
.black-card__label svg {
  aspect-ratio: 1/1;
  width: 51%;
}
@media (max-width: 768px) {
  .black-card__label {
    border-radius: 0.375rem;
    width: 2rem;
  }
}
.black-card__row {
  display: flex;
  align-items: start;
  justify-content: space-between;
}
.black-card__count {
  font-weight: 700;
  font-size: 1rem;
}
@media (max-width: 576px) {
  .black-card__count {
    opacity: 0;
  }
}
.black-card__title {
  font-weight: 700;
  font-size: 1rem;
  margin-bottom: 0.3125rem;
}
.black-card__description {
  font-size: 0.75rem;
}

/*---------------------------------------------------------------------------------------------------------------------------------------------
Cases card
-----------------------------------------------------------------------------------------------------------------------------------------------*/
.cases-card {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
  transition: all 0.2s ease-in-out;
}
.cases-card .playlabel {
  z-index: 1;
}
.cases-card--video {
  position: relative;
}
.cases-card--video .cases-card__image:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-color: rgba(198, 157, 7, 0.25);
}
.cases-card:hover {
  filter: brightness(1.3);
}
.cases-card:hover .cases-card__text {
  color: #412500;
}
.cases-card__image {
  position: relative;
  aspect-ratio: 1/1;
  overflow: hidden;
  width: 100%;
  border-radius: 1.25rem;
  border: 1px solid transparent;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  margin-bottom: 0.625rem;
}
.cases-card__image:hover {
  border: 1px solid var(--gold);
}
.cases-card__image:hover img {
  transform: scale(1.05);
}
.cases-card__image img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
.cases-card__tags {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 0.3125rem;
  margin-bottom: 0rem;
}
.cases-card__tag {
  border-radius: 2.25rem;
  padding: 4px 8px 6px;
  background-color: var(--black);
  font-weight: 700;
  font-size: 0.625rem;
  text-align: center;
  color: var(--white);
}
.cases-card__text {
  transition: all 0.3s ease-in-out;
  color: var(--black);
}
.cases-card__title {
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 120%;
  margin-bottom: 0.625rem;
}
.cases-card__description {
  font-size: 0.75rem;
  line-height: 120%;
}

/*---------------------------------------------------------------------------------------------------------------------------------------
NUMBERS
----------------------------------------------------------------------------------------------------------------------------------------*/
.numbers {
  color: var(--black);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (max-width: 768px) {
  .numbers {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 2.5rem;
  }
}
.numbers__item {
  display: inline-flex;
  gap: 0.9375rem;
  align-items: center;
}
@media (max-width: 768px) {
  .numbers__item {
    flex-direction: column;
  }
}
.numbers__label {
  position: relative;
  aspect-ratio: 1/1;
  width: 2.625rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-weight: 700;
  font-size: 1rem;
  line-height: 100%;
  text-align: center;
  color: var(--white);
  background: #262626;
}
.numbers__label::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 100%;
  width: 1px;
  height: 5px;
  background-color: var(--black);
}
@media (max-width: 576px) {
  .numbers__label::before {
    content: none;
  }
}
.numbers__label--gold {
  color: #4e421b;
  background: var(--gradient);
}
@media (max-width: 576px) {
  .numbers__label {
    width: 3.75rem;
  }
}
.numbers__text {
  font-weight: 700;
  font-size: 1rem;
  line-height: 120%;
}
@media (max-width: 576px) {
  .numbers__text {
    font-size: 1.125rem;
  }
}

/*---------------------------------------------------------------------------------------------------------------------------------------
Playlabel
----------------------------------------------------------------------------------------------------------------------------------------*/
.playlabel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 100%;
  color: #4e421b;
  aspect-ratio: 1/1;
  max-width: 4.75rem;
  width: 17%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: var(--gradient);
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.25);
}
.playlabel svg {
  transition: all 0.2s ease-in-out;
  aspect-ratio: 1/1;
  width: 33%;
  margin-left: 2px;
}
.playlabel:hover {
  transform: translate(-50%, -50%) scale(1.2);
}
.playlabel:hover svg {
  transform: scale(1.75);
}

/*---------------------------------------------------------------------------------------------------------------------------------------
White card
----------------------------------------------------------------------------------------------------------------------------------------*/
.white-card {
  color: var(--black);
  border-radius: 1.25rem;
  padding: 1.25rem;
  backdrop-filter: blur(19px);
  background: var(--white);
  display: flex;
  align-items: center;
  gap: 1.25rem;
  min-height: 5rem;
  height: 100%;
}
@media (min-width: 1201px) {
  .white-card {
    max-height: 5rem;
    height: 100%;
  }
}
.white-card--vertical {
  min-height: 5rem;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 7.5rem;
  gap: 1.25rem;
