/* --------------------------------------------------------------------------------------------------------------------------
MAIN THIRD
--------------------------------------------------------------------------------------------------------------------------------*/
.main-whatwedoing {
	color: var(--white);
	
	&__block {
		display: flex;
		flex-direction: column;
		min-height: rem(659);
		background-image: url("../../images/main/webp/main-3-bg.webp");
	}

	.section__title{
		margin-bottom: rem(67);
		@include sm{
			margin-bottom: rem(40);
		}
	}

	&__row {
		position: relative;

		display: flex;		
		justify-content: space-between;
		gap: rem(40);

		flex-grow: 1;

		@include md {
			justify-content: center;
			flex-wrap: wrap;
		}
	}
	.colum-1 {
		position: relative;
		align-self: start;
	}
	.column-2 {
		position: relative;
		align-self: end;
	}

	&__list {
		max-width: rem(387);
		color: var(--black);

		display: flex;
		flex-direction: column;
		gap: rem(18);

		border-radius: rem(20);
		padding: rem(30);
		min-width: rem(208);

		background: var(--white);

		li {
			position: relative;

			font-weight: 700;
			font-size: rem(16);

			padding-left: rem(35);

			&:before {
				content: "";
				position: absolute;
				top: rem(5);
				left: 0;
				background-image: url("data:image/svg+xml;base64,");
				background-repeat: no-repeat;
				background-size: cover;
				width: rem(14);
				height: rem(12);
			}
		}
	}

	/*---------------------------------------------------------------------------------------------------------------------------------------
    3 ICONS 
    ---------------------------------------------------------------------------------------------------------------------------------------------*/
	.ai,
	.tiktok,
	.laptop {
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 1;

		content: "";
		position: absolute;
		backdrop-filter: blur(24px);
		box-shadow: 10px 8px 27px 0 rgba(0, 0, 0, 0.36), 39px 31px 50px 0 rgba(0, 0, 0, 0.31), 88px 69px 67px 0 rgba(0, 0, 0, 0.18),
			157px 123px 80px 0 rgba(0, 0, 0, 0.05), 245px 192px 87px 0 rgba(0, 0, 0, 0.01), -1px 0 1px rgba(255, 255, 255, 0.9);
		background: rgba(39, 39, 49, 0.1);

		border-radius: rem(20);
		width: rem(100);
		height: rem(100);
	}

	.ai {
		left: -28.9%;
		top: -47%;
		@include md {
			left: -18%;
		}

		&-icon {
			width: 45%;
			aspect-ratio: 1/1;
			background-image: url("data:image/svg+xml;base64,");
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100%;
		}
	}

	.laptop {
		right: -20%;
		bottom: rem(-35);
		&-icon {
			width: 45%;
			aspect-ratio: 1/1;
			background-image: url("data:image/svg+xml;base64,");
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100%;
		}
	}
	.tiktok {
		left: -16%;
		bottom: -3.175rem;
		&-icon {
			width: 45%;
			aspect-ratio: 1/1;
			background-image: url("data:image/svg+xml;base64,");
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100%;
		}
	}
}
